.about {
  padding: 80px 104px;
  color: #1a1b22;
}

.about__container {
  display: flex;
  align-items: center;
}

.about__avatar {
  border-radius: 50%;
  width: 464px;
  height: 464px;
  object-fit: contain;
  overflow: hidden;
}

.about__description-container {
  margin-left: 56px;
  max-width: 600px;
}

.about__title {
  font-family: 'Roboto Slab', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 40px;
  line-height: 46px;
  margin-top: 0;
  margin-bottom: 24px;
}

.about__paragraf {
  font-family: 'Roboto', Arial, sans-serif;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  margin-top: 0;
}

@media screen and (max-width: 800px) {
  .about {
    padding: 61px 40px;
  }

  .about__avatar {
    width: 232px;
    height: 232px;
  }

  .about__description-container {
    margin-left: 32px;
    max-width: 424px;
  }

  .about__title {
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 16px;
  }
}

@media screen and (max-width: 620px) {
  .about {
    padding: 32px 16px 68px;
  }

  .about__container {
  flex-direction: column;

}

  .about__avatar {
    width: 272px;
    height: 272px;
  }

    .about__title {
       margin-top: 24px;

  }
}
