.news-card {
  box-sizing: border-box;
  display: flex;
  position: relative;
  flex-direction: column;
  border-radius: 16px;
  overflow: hidden;
  align-items: center;
  background-color: #ffffff;
}

.news-card__title {
  font-family: 'Roboto Slab', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 30px;
  color: #1a1b22;
  margin-top: 12px;
  margin-bottom: 0;
  max-height: 90px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.news-card__text {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #1a1b22;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-top: 16px;
  margin-bottom: 0;
}

.news-card__keyword-container {
  background-color: #ffffff;
  box-sizing: border-box;
  padding: 8px 12px;
  max-width: 159px;
  height: 40px;
  border-radius: 10px;
  position: absolute;
  top: 24px;
  left: 24px;
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #000000;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.news-card__keyword {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #000000;
  margin-top: 0;
  margin-bottom: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.news-card__tooltip {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  align-items: center;
  text-align: center;
  justify-content: center;
  color: #000000;

  background-color: #ffffff;
  box-sizing: border-box;
  margin-top: 0;
  margin-bottom: 0;
  width: 159px;
  height: 40px;
  border-radius: 10px;
  position: absolute;
  top: 24px;
  right: 69px;

  display: none;
  transition: 0.3s easy;
  z-index: 2;
}

.news-card__date {
  font-family: 'Source Sans Pro', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  color: #b6bcbf;
  margin-top: 0;
  margin-bottom: 0;
}

.news-card__source {
  font-family: 'Roboto Slab', Arial, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  color: #b6bcbf;
  margin-top: 0;
  margin-bottom: 24px;
  padding: 0 24px;
}

.news-card__img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 272px;
}

.news-card__btn {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  cursor: pointer;
  border: 0;
  top: 24px;
  right: 24px;
}

.news-card__btn:focus {
  outline: none;
}

.news-card__btn_type_trash {
  background-image: url(../../images/icons/trash.svg);
}

.news-card__btn_type_trash:hover {
  background-image: url(../../images/icons/trash-hover.svg);
  transition: 0.3s easy;
}

.news-card__btn_type_save {
  background-image: url(../../images/icons/bookmark-normal.svg);
}

.news-card__btn_type_save-marked {
  background-image: url(../../images/icons/bookmark-marked.svg);
}

.news-card__btn_type_save:hover {
  background-image: url(../../images/icons/bookmark-hover.svg);
  transition: 0.3s ease;
}

.news-card__btn_type_trash:hover + .news-card__tooltip {
  display: flex;
  transition: 0.3s easy;
}

.news-card__btn_type_save:hover + .news-card__tooltip {
  display: flex;
  transition: 0.3s easy;
  font-size: 10px;
  line-height: 12px;
}

.news-card__link {
  text-decoration: none;
  cursor: pointer;
  width: 100%;
}

.news-card__info {
  padding: 20px 24px 18px;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 800px) {
  .news-card__title {
    max-height: 74px;
    font-size: 22px;
    line-height: 24px;
  }

  .news-card__text {
    max-height: 68px;
    margin-top: 10px;
  }

  .news-card__keyword-container {
    top: 8px;
    left: 8px;
  }

  .news-card__tooltip {
    top: 8px;
    right: 52px;
  }

  .news-card__source {
    margin-bottom: 16px;
    padding: 0 16px;
  }

  .news-card__img {
    height: 150px;
  }

  .news-card__btn {
    top: 8px;
    right: 8px;
  }

  .news-card__info {
    padding: 16px 16px 12px;
  }
}

@media screen and (max-width: 432px) {
  .news-card__title {
    max-height: 56px;
    line-height: 26px;
    margin-top: 10px;
  }

  .news-card__info {
    padding-bottom: 8px;
  }
  .news-card__source {
    margin-bottom: 17px;
  }
}
